import "../scss/realtime_stock_order.scss";

// 初始化 TradingView Widget
const widget = new TradingView.widget({
    container_id: "tradingview-chart",
    symbol: "TSM", // 台積電美股代號
    interval: "D",
    timezone: "Asia/Taipei",
    theme: "dark",
    style: "1",
    locale: "zh_TW",
    height: "100%",
    width: "100%",
    enable_publishing: false,
    hide_side_toolbar: false,
    allow_symbol_change: false,
});



// 按鈕互動邏輯
const sellBtn = document.getElementById("sell-btn");
const buyBtn = document.getElementById("buy-btn");

// 切換按鈕狀態
function toggleActiveButton(selectedButton) {
    if (selectedButton === "sell") {
        sellBtn.classList.add("active");
        sellBtn.classList.remove("gray");
        sellBtn.style.color = "white";

        buyBtn.classList.remove("active");
        buyBtn.classList.add("gray");
        buyBtn.style.color = "black";
    } else {
        buyBtn.classList.add("active");
        buyBtn.classList.remove("gray");
        buyBtn.style.color = "white";

        sellBtn.classList.remove("active");
        sellBtn.classList.add("gray");
        sellBtn.style.color = "black";
    }
}

sellBtn.addEventListener("click", () => toggleActiveButton("sell"));
buyBtn.addEventListener("click", () => toggleActiveButton("buy"));

// 初始化：預設買入按鈕為綠色
toggleActiveButton("buy");

// 切換市價單與限價單邏輯
const orderType = document.getElementById("order-type");
const priceInputContainer = document.getElementById("price-input-container");

orderType.addEventListener("change", () => {
    if (orderType.value === "limit") {
        priceInputContainer.style.display = "block"; // 顯示限價單輸入欄位
    } else {
        priceInputContainer.style.display = "none"; // 隱藏限價單輸入欄位
    }
});

// 數量增減邏輯
document.getElementById("decrease-quantity").addEventListener("click", () => {
    const quantityInput = document.getElementById("quantity");
    quantityInput.value = Math.max(1, parseInt(quantityInput.value) - 1);
});

document.getElementById("increase-quantity").addEventListener("click", () => {
    const quantityInput = document.getElementById("quantity");
    quantityInput.value = parseInt(quantityInput.value) + 1;
});

// 確認下單邏輯
// 確認下單邏輯
document.getElementById("place-order").addEventListener("click", () => {
    const priceInput = document.getElementById("limit-price"); // 價格輸入欄位
    const orderType = document.getElementById("order-type").value; // 訂單類型
    const quantity = document.getElementById("quantity").value; // 數量
    const sellBtn = document.getElementById("sell-btn");
    const action = sellBtn.classList.contains("active") ? "賣出" : "買入";

    // 檢查是否為限價單且價格輸入為空
    if (orderType === "limit" && (!priceInput.value || isNaN(priceInput.value))) {
        alert("請輸入價格！"); // 提示使用者輸入價格
        priceInput.focus(); // 將焦點移到價格欄位
        return; // 停止執行後續邏輯
    }

    const price = orderType === "limit" ? priceInput.value : "市價";

    // 成功下單提示
    alert(`下單成功！\n動作：${action}\n類型：${orderType === "limit" ? "限價單" : "市價單"}\n價格：${price}\n數量：${quantity}`);
});
document.getElementById("back-button").addEventListener("click", () => {
    history.back(); // 使用瀏覽器的返回功能
  });





