olor: $green;\r\n            color: $white;\r\n            width: 100%;\r\n            text-align: center;\r\n            padding: 10px;\r\n            border: none;\r\n            border-radius: 5px;\r\n            cursor: pointer;\r\n        }\r\n        .back-button {\r\n            position: absolute; // 使用絕對定位\r\n            bottom: 20px; // 向下移動距離\r\n            right: 30px; // 向右移動距離\r\n            background-color: $gray; // 按鈕背景顏色\r\n            color: $black; // 按鈕文字顏色\r\n            padding: 5px 15px; // 內邊距\r\n            border-radius: 5px; // 圓角\r\n            border: none; // 去除邊框\r\n            cursor: pointer; // 游標效果\r\n            font-size: 0.9rem; // 字體大小\r\n            display: inline-block; // 行內塊顯示模式\r\n            text-align: center; // 文本置中\r\n            transition: background-color 0.3s ease; // 加入滑順的背景顏色變化效果\r\n          \r\n            &:hover {\r\n              background-color: #000000;// 滑鼠懸停時背景顏色\r\n              color: #ffffff; // 滑鼠懸停時文字顏色\r\n            }\r\n          }\r\n    }\r\n}\r\n\r\n"],"sourceRoot":""}]);
